import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';

const useStyles = makeStyles((theme) => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function ModalList(props) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const {data} = props;
  let kmTotal = data[0].costo*(data.length);
  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <button type="button" onClick={handleOpen}>
        Ver lista
      </button>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
            <h4 id="transition-modal-title">Lista de informes</h4>
            {
                data.map((informe,index, kmTotal)=>                  
                    <p id="transition-modal-description" key={index} >{informe.datos} - {informe.costo}</p>
                    // kmTotal = kmTotal + informe.costo

                                        
                )

            }
              <p> <b>KM Total : {kmTotal}</b> </p>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
